import { userService } from "../../services/UserService"

export function spendCoins(amount) {
  return async (dispatch) => {
    dispatch({ type: "SPEND_COINS", amount })
  }
}

export function addMove(contact, transferAmount) {
  return async (dispatch) => {
    const move = userService.addMove(contact, transferAmount)
    dispatch({ type: "ADD_MOVE", move })
  }
}
export function loadUser() {
  return async (dispatch) => {
    const loggedInUser = userService.getUser()
    dispatch({ type: "SET_USER", loggedInUser })
  }
}
