const INITIAL_STATE = {
  loggedInUser: {
    name: "Jorji",
    coins: 100,
    moves: [],
  },
}

export function userReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "SET_USER":
      return { ...state, loggedInUser: action.loggedInUser }
    case "SPEND_COINS":
      return {
        ...state,
        loggedInUser: {
          ...state.loggedInUser,
          coins: state.loggedInUser.balance - action.amount,
        },
      }
    case "ADD_MOVE":
      return {
        ...state,
        loggedInUser: {
          ...state.loggedInUser,
          moves: [...state.loggedInUser.moves, action.move],
        },
      }
    default:
      return state
  }
}
