import { Component } from "react"
import { userService } from "../services/UserService"
import { connect } from "react-redux"
import { addMove, loadUser } from "../store/actions/UserActions"

class _TransferFund extends Component {
  state = {
    transferAmount: 0,
  }
  async componentDidMount() {
   
    await this.props.loadUser()
  }


  onTransferCoins = (e) => {
    e.preventDefault()
    const { transferAmount } = this.state
    const { contact, user } = this.props
    if (transferAmount <= 0) return console.error("You too cheap")
    if (user.coins < transferAmount) return console.error("Your are too poor")
    this.props.addMove(contact, transferAmount)
  }
  handelChange = ({ target }) => {
    const value = target.value

    this.setState({ transferAmount: value })
  }
  render() {
    const { contact, user } = this.props
    const { transferAmount } = this.state
    if (!user) return <div>Loading...</div>
    return (
      <div className='transfer-action'>
        <h1 className='transfer-title'>Transfer coins to {contact.name}:</h1>
        <span className='alert'>
          {user.coins <= 0 && "No more coins to transfer"}
        </span>
        <form
          className='transfer-form flex space-between align-center'
          onSubmit={this.onTransferCoins}
        >
          <label htmlFor='amount'>Amount ({transferAmount}):</label>
          <input
            value={transferAmount}
            title={transferAmount}
            type='range'
            id='amount'
            name='amount'
            min={1}
            step={1}
            max={user.coins}
            onChange={this.handelChange}
          />
          <button>Transfer</button>
        </form>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.userModule.loggedInUser,
  }
}

const mapDispatchToProps = {
  addMove,
  loadUser,
}

export const TransferFund = connect(
  mapStateToProps,
  mapDispatchToProps
)(_TransferFund)
